export default {
  data(){
	  return {
		lPostion:0
	  }
  },
  props: {
      arrow:{
       type:String,
       default:'true'
     },
     list:{
       type:Array,
	   default: () => {
		   return []
	   }
     },
     index:{
       type:String,
       default:'0'
     },
     type:{
       type:String,
       default:'default'
     }
  },
  components: {}, // 声明页面中所引用的组件，或声明组件中所引用的子组件

  methods: {
    leftChange(e){
        this.lPostion=e.detail.scrollLeft
    },
    toRight(){
         this.lPostion=this.customerData.lPostion+100
    },
    itemTap(index){
      // this.index=index;
      // this.$emit("item-change",index)
    }
  }
}
